.about {
  width: 100%;
  height: 89vh;
  display: flex;
  flex-direction: column;
}

.aboutTop {
  width: 100%;
  height: 50%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.aboutBottom {
  display: flex;
  flex-direction: column;
  height: 50%;
  align-items: center;
  margin-top: 30px;
}

.aboutBottom p {
  width: 70%;
  margin: 20px;
  padding: 10px;
}

.about .aboutBottom h1 {
  font-weight: 400;
  font-size: 70px;
  color: black;
  height: 40px;
  display: flex;
  justify-content: center;
  transform: translateY(-40px);
}

@media only screen and (max-width: 650px) {
  .about {
    width: 100%;
    height: 180vh;
    display: flex;
    flex-direction: column;

  }

}