.active {
  color: aqua;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #121619;
  color: white;
  position: sticky;
  top: 0;
}

.navbar .logo {
  padding-left: 160px;
  width: 70px;
}

nav .nav-links {
  padding-right: 20rem;
}

nav ul {
  display: flex;
}

nav ul li {
  list-style: none;
}

nav ul li a {
  display: block;
  text-decoration: none;
  font-size: 1.25rem;
  color: white;
  padding: 0.5rem;
  margin: 0 0.5rem;
  border-radius: 0.5rem;
}

nav ul li a:not(.active):hover {
  background-color: #ffffff;
  color: #000;
  font-size: 1.25rem;


}

nav .menu {
  display: none;
  position: absolute;
  top: 0.75rem;
  right: 0.5rem;
  flex-direction: column;
  justify-content: space-between;
  width: 2.25rem;
  height: 2rem;
}

.navbar .menu button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.menu button:focus {
  outline: none;
  /* Remove the default focus border */
}

.menu button svg {
  font-size: 1.5rem;
  color: white;
}

.larger-icon {
  font-size: 2rem;

}

.larger-icon:not(.active):hover {
  font-size: 2rem;
}

@media (max-width: 480px) {
  nav .menu {
    display: flex;
  }

  .navbar .logo {
    margin-top: 10px;
    height: 100%;
    padding-left: 30px;
  }

  nav {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
  }

  nav ul {
    display: none;
    flex-direction: column;
    width: 100%;
    margin-bottom: 0.25rem;

  }

  nav ul li a:not(.active):hover {
    background-color: #ffffff;
    color: #000;
    font-size: 1.25rem;

    .larger-icon {
      font-size: 2rem;
    }

    .larger-icon:not(.active):hover {
      font-size: 2rem;
    }

  }

  nav ul.open {
    display: flex;
  }

  nav ul li {
    width: 80%;
    text-align: center;
    font-size: 1.25rem;
  }

  nav ul li a {
    margin: 0.2rem 0.5rem;
  }

  .menu button {
    display: flex;
    align-items: center;
  }
}