/* Login.css */



/* Login Container */
.login-container {
    width: 80%;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 400px;
    background-color: #f8f9fa;
    margin-top: 100px;
    margin-bottom: 100px;
    border-radius: 30px;
    backdrop-filter: blur(20px);
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 30px rgba(8, 7, 16, 0.6);
    padding: 30px 25px;
}

/* Title Styling */
.title {
    text-align: center;
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 20px;
}

/* Text Styling */
.text {
    font-weight: 500;
    padding-bottom: 5px;
}

/* Input Styling */
input {
    width: 350px;
    padding: 5px 25px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

/* Password Input Container */
.password-input-container {
    position: relative;
    margin-bottom: 15px;
}

/* Toggle Password Icon Styling */
.password-input-container .toggle-password-icon {
    font-size: 20px;
    position: absolute;
    top: 15px;
    right: 25px;
}

/* Back Link Styling */
.back {
    text-align: center;
    font-size: 20px;
    cursor: pointer;
    margin-top: 30px;
}

/* Responsive Styling */
@media only screen and (max-width: 600px) {
    .login-container {
        width: 80%;
        margin-top: 50px;
        margin-bottom: 50px;
    }

    input {
        width: 100%;
    }

    .password-input-container {
        margin-bottom: 20px;
    }

    .title {
        font-size: 20px;
        margin-bottom: 15px;
    }

    .text {
        font-size: 16px;
    }

    .toggle-password-icon {
        font-size: 16px;
        top: 10px;
    }

    .button-container {
        flex-direction: column;
    }

    .back {
        font-size: 18px;
        margin-top: 20px;
    }
}